import type { ImageData } from '../../mo/models';
import isValidNumber from './isValidNumber';

export const isComplexImg = (data: ImageData) => {
  const {
    width, height, rotation, rectClip = {},
  } = data;
  const { left = 0, right = 0, bottom = 0, top = 0 } = rectClip;
  const isClip = left || right || bottom || top;
  const isRotated = isValidNumber(rotation) &&
    rotation !== 0;
  return typeof width === 'number' &&
    typeof height === 'number' &&
    (isRotated || isClip);
};

export function isValidSize(width, height) {
  return isValidNumber(width) &&
    isValidNumber(height) &&
    width !== 0 && height !== 0;
}
